.App{
  display: flex;
  justify-content: center;
  align-items: center;
}
.container{
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.background-img{
  height: 100%;
  width: 100%;
}

.label-em-breve{
  font-family: 'league-gothic';
  color: rgb(168,135,18);
  font-size: 45px;
  text-align: center;
}

.label-sigam-nos{
  font-family: 'Kirvy-Bold';
  padding: 16px;
  text-align: center;
}

.label-link-site{
  font-family: 'Kirvy-Bold';
  padding-top: 8px;
  padding-bottom: 38px;
  font-size: 24px;
  text-align: center;
}

.list-icons-social-media{
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 330px;
}

@media only screen and (min-width: 500px) {
  
  svg{
    padding: 10px;
    width: 30%;
    height: 12%;
  }

  .label-em-breve{
    font-size: 70px;
  }
  
  .label-sigam-nos{
    padding: 16px;
    font-size: 20px;
  }
  
  .label-link-site{
    padding-top: 8px;
    padding-bottom: 38px;
  }
  
  .list-icons-social-media{
    width: 420px;
  }
}

.instagram img:hover{
  background-color: rgb(236,229,213,63%);
  border-radius: 50px;
  box-shadow: inset 5em 1em rgb(236,229,213);
}
.facebook img:hover{
  background-color: rgb(236,229,213,63%);
  border-radius: 50px;
  box-shadow: inset 5em 1em rgb(236,229,213);
}
.whatsapp img:hover{
  background-color: rgb(236,229,213,63%);
  border-radius: 50px;
  box-shadow: inset 5em 1em rgb(236,229,213);
}
.email img:hover{
  background-color: rgb(236,229,213,63%);
  border-radius: 50px;
  box-shadow: inset 5em 1em rgb(236,229,213);
}

.logo{
  position: absolute;
  top: 0;
  left: 0;
  padding: 20px;
  width: 20%;
  height: 20%;
}